.dropdown-alerts-custom {
  width: 420px !important;
  max-height: 400px;
  overflow-y: auto;
}

.project-table {

  &__wrap {
    margin-top: 20px;
    overflow: auto;
    max-height: 250px;
  }

}

.function-btn {

  &__module-open-btn {
    display: none;
  }

  &__margin {
    margin-top: 20px;
  }

  &__wrap {
    display: inline-block;
    padding: 0 20px 15px 0;
    vertical-align: top;
  }

  &__daterange {
    display: inline-block;
    width: 180px;
    color: #333;
    border: 1px solid #f8ac59;
    font-weight: 400;
    margin-left: 10px;
    font-size: 12px;
    line-height: 1.5;
    border-radius: 3px;
    padding: 4px;

    & input {
      display: inline-block;
      text-align: center;
      width: 49%;
      border: none;
    }

  }

  &__detail {
    color: #333;
    border: 1px solid #f8ac59;
    font-weight: 400;
    padding: 5px 10px;
    margin-left: 10px;
    font-size: 12px;
    line-height: 1.5;
    border-radius: 3px;
  }

  //&__chosen-select {
  //  width: 300px;
  //}

}

@media only screen and (max-width: 992px) {

  .dropdown-alerts-custom {
    margin-left: 0 !important;
  }

  .function-btn {

    &__module-open-btn {
      display: block;
      padding: 10px;
      margin-bottom: 20px;
      text-align: center;
      background-color: #23c6c8;
      border:1px solid #23c6c8;
      border-radius: 3px;
      color: #FFFFFF;
      font-size: 14px;
      font-weight: 400;
      line-height: 1.42857143;
      white-space: nowrap;
      vertical-align: middle;
      cursor: pointer;
      user-select: none;

      &:hover {
        background-color: #21b9bb;
        border-color: #21b9bb;
      }

      &:hover {
        background-color: #21b9bb;
        border-color: #21b9bb;
      }

      &:active {
        background-image: none;
        -webkit-box-shadow: inset 0 3px 5px rgba(0, 0, 0, .125);
        box-shadow: inset 0 3px 5px rgba(0, 0, 0, .125);
      }

    }

    &__module {
      display: none;

      &_open {
        display: block;
      }

    }

    &__wrap {
      display: block;
      padding-right: 0;
    }

    &__daterange {
      width: 100%;
      margin: 10px 0 0 0;
    }

    &__detail {
      width: 100%;
      margin: 10px 0 0 0;
    }

  }

  .btn-group > .btn:first-child:not(:last-child):not(.dropdown-toggle) {
    border-top-right-radius: 3px;
  }

}

