.data-table {
  width: 100%;
  max-height: 500px;
  //overflow: auto;

  &:after {
    content: " ";
    visibility: hidden;
    display: block;
    height: 0;
    clear: both;
  }

  &__head {
    float: left;
    width: 300px;
    background: #f8f8f8;

    &-title {
      padding: 10px;
      border-bottom: 1px solid #DDDDDD;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;

      //&:first-child {
      //  border-top: 1px solid #DDDDDD;
      //}

    }

  }

  &__values {
    margin-left: 300px;
    white-space: nowrap;

    &-col {
      display: inline-block;
      text-align: center;

    }

  }

  &__value {
    display: block;
    padding: 10px;
    border-bottom: 1px solid #DDDDDD;
    margin-right: -4px;

    &_max {
      color: #fff;
      background: #F44336;
    }

  }

}

@media(max-width: 767px) {
  .data-table {
    overflow: auto;
  }
}

@media(min-width: 768px) {
  .data-table {
    overflow: auto;
  }
}

@media(min-width: 992px) {
  .data-table {
    overflow: auto;

    &__values {
      overflow: auto;
    }

  }
}

